.footer__menu {
  display: flex;
  justify-content: center;
  margin-bottom: 60px;

  text-align: center;
  .column__article {
    display: block;
    margin-bottom: 20px;
    font-size: 18px;
    line-height: 21px;
  }
  .footer_column {
    margin: 0px 100px;
    z-index: 2;
    font-size: 15px;
    line-height: 18px;
  }
  a {
    padding: 15px 0;
    display: block;
    text-decoration: none;
  }
  a:link,
  a:visited,
  a:hover {
    color: #6882a1;
  }
}
